.products {
    padding: 1rem 2rem;
    width: 100%;
    padding-top: 8rem;
      
    ul {
      display: flex;
      flex-wrap: wrap;
    }
    h4 {
      margin: 0;
      font-weight: normal;
      font-size: 0.9rem;
    }
    span {
      margin: 0;
      font-size: 0.75rem;
    }
  
    input {
      margin: 0 1rem 0 0.25rem;
    }
  
    .addCart {
      font-size: 0.25rem;
      color: rgb(232, 230, 227);
      background-color: rgb(51, 55, 58);
      padding: 0.5rem 2rem;
      margin: 0.25rem;
      border: none;
      transform: scale(0.85);
    }
  
    .banner {
      max-width: 100%;
      margin-top: 1rem;
    }
  
    .card {
      margin: 0.25rem;
      margin-bottom: 1rem;
      text-align: center;
      flex: 30%;
      img {
        max-width: 25vw;
        transition: 0.25s;
        &:hover,
        &:focus {
          text-decoration: none;
          outline: none;
          opacity: 0.8;
          color: #999999;
        }
      }
  
      &__title {
        font-size: 0.9rem;
        color: #444;
        margin-bottom: 0.25rem;
        transition: 0.25s;
        letter-spacing: 0.5px;
        &:hover,
        &:focus {
          text-decoration: none;
          outline: none;
          opacity: 0.8;
          color: #999999;
        }
      }
      &__price {
        text-decoration: line-through;
        margin-right: 1rem;
      }
      &__promotePrice {
        color: rgb(192, 0, 0);
      }
    }
  
  }
  
  @media(max-width: 768px){
    .products {
      span {
        font-size: 1rem;
      }
      .card {
        flex: 80vw;
        img {
          max-width: 80vw; 
        }
        &__title {
          font-size: 1rem;
          margin-bottom: .5rem;
        }
      }
      .addCart {
        margin-top: .5rem;
        transform: scale(1);
      }
    }
  }