.carousel {
    width: 100%;
    height: 100vh;
    position: relative;
    overflow: hidden;
    margin-top: 1rem;

    img {
        width: 100vw;
    }

    button {
        z-index: 5;
        outline: none;
        padding: 20px;
    }

    button[data-slide="next"] {
        left: initial;
        right: 0;
    }
}

.carouselInner {
    width: 100vw;
    height: 100vh;
    white-space: nowrap;
    overflow: hidden;
    word-spacing: -1em;
}

.carouselItem {
    width: 100%;
    height: 100vh;
    overflow: hidden;
    display: inline-block;
    position: relative;
    transition: 0.5s;
    margin: 0;
    word-spacing: 0;
    left: 0;

    img {
        object-fit: cover;
        height: 100vh;
    }
}

.control {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    box-shadow: none;
    border: none;
    background-color: transparent;
    transition: 0.5s;
    font-size: 1.25rem;
    cursor: pointer;
    color: rgba(white, 0.5);
    opacity: 0.2;
    &:focus {
        border: none;
        outline: none;
    }
}
