
.outbox {
    background-color: rgba(59, 57, 55, 0.74);
    position: fixed;
    top: 0;
    left: 0;
    z-index: 99;
    height: 100vh;
    width: 100vw;
    display: flex;
    p, span {
      font-size: 0.8rem;
    }
  
    .box {
      background-color: white;
      border-radius: 0.5rem;
      border: none;
      padding: 1rem;
      background-color: #fff;
      height: 120px;
      width: 300px;
      margin: auto;
      text-align: center;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
    }
    .btn {
      margin: 0.5rem;
      margin-bottom: 0;
      background-color: #444;
      color: white;
      padding: 0.5rem 3.5rem;
      display: inline-block;
      border-radius: 0.25rem;
      cursor: pointer;
      font-size: 0.8rem;
      transition: 0.5s;
  
      &:hover {
        opacity: 0.9;
      }
      &.small {
        padding: 0.5rem 1.5rem;
      }
    }
    .alert {
      position: absolute;
      width: 100vw;
      height: 100vh;
      background-color: rgba(black, 0.8);
      z-index: 100;
    }
    
  }
  
  .displayNone {
    display: none;
  }
  